body, * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: white;
}
body {
    background-color: #FFF6E9;
}
.weather-app {
    h1 {
        background-color: #40A2E3;
        margin-bottom: 20px;
        padding: 5px;
        box-shadow: 3px 6px 8px 0px rgba(74,195,255,0.72);
    }
}
.weather-card {
    background-color: #0D9276;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 80%;
    margin: auto;
    margin-top: 1rem;
    h2 {
        font-size: 24px;
        margin-bottom: 10px;
    }
} 
@media only screen and (min-width:768px) {
    .weather-card {
        width: 40rem;
    }
}
.weather-condition {
    display: flex;
    align-items: center;
    gap: .4rem;
    margin: 10px 0;
} 

